.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  gap: 2rem;
  width: 100%;
  height: 100%;
}

.quesion-num {
  position: absolute;
  top: 3rem;
  left: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 12rem;
  height: 12rem;
  border: 0.8rem solid white;
  border-radius: 50%;
  font-size: 2.2rem;
  font-family: 'Mochiy Pop P One', sans-serif;

  p {
    color: white;
  }
}
@media screen and (max-width: 479.98px) {
  .quesion-num {
    width: 10rem;
    height: 10rem;
  }
}
