.btn{position:relative;color:#fff;font-size:2rem;background-color:unset;transition:all .3s ease-in-out;padding:1.5rem 1.5rem 1.5rem 3rem;border-radius:1rem}.btn:hover{background-color:rgba(255,255,255,.3)}.btn::before{content:"";position:absolute;top:50%;transform:translateY(-50%);left:10px}.right{background-color:rgba(0,255,60,.5)}.right::before{content:""}.wrong{background-color:rgba(255,112,112,.5)}.wrong::before{content:""}.disabled{pointer-events:none}